.proj-card{
    border-radius: 8px;
    width: 27%;
    margin: 1% 3.16%;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-align: center;
        align-items: center;
    padding: 1%;
    background-color: #fff;
    -webkit-box-shadow: 0 0 20px 0 rgba(0, 0, 0, 0.2), 0 5px 5px 0 rgba(0, 0, 0, 0.24);
            box-shadow: 0 0 20px 0 rgba(0, 0, 0, 0.2), 0 5px 5px 0 rgba(0, 0, 0, 0.24);
    z-index: 1;
}

.card-pic{
    width: 100%;
    -o-object-fit: cover;
       object-fit: cover;
}

.description{
    margin-left: 2%;
    margin-right: 2%;
}

.github{
    padding: 0;
    margin: 0;
}

.link-site{
    margin: 0;
}

@media(max-width: 900px) {
    .proj-card{
        width: 40%;
        margin: 2% 5%;
    }
}

@media(max-width: 500px) {
    .proj-card{
        width: 85%;
        margin: 2% auto;
    }
}


